@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300&family=Oswald:wght@600&display=swap');
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
}
html {
  overflow-y: scroll;
}
main {
  width: 100%;
  height: 100%;
  background-color: aliceblue;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}
/* ...................................................................................................................... */
header {
  width: 100%;
  height: 10%;
  background-color: rgb(6, 109, 150);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  z-index: 1;
}
header h1 {
  padding: 10px;
  width: 50%;
  height: 100%;
  font-family: 'Oswald', sans-serif;
  font-size: 4rem;
  color: whitesmoke;
}
header h3 {
  padding: 45px 140px;
  width: 50%;
  height: 100%;
  font-family: 'Oswald', sans-serif;
  /* font-size: 4rem; */
  color: whitesmoke;
}
header aside {
  width: 650px;
  padding: 35px 0;
}
header aside form {
  display: flex;
  width: inherit;
  height: 100px;
}
header aside form input[type='text'] {
  width: 200px;
  height: 40px;
  background-color: whitesmoke;
  padding: 5px 10px;
  outline: none;
  border: none;
  font-size: 1.2rem;
  color: #303131;
}
.search-button {
  width: 50px;
  height: 50px;
  padding-left: 10px;
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
}
.search-icon {
  width: 30px;
  height: 30px;
  color: whitesmoke;
}
header label {
  color: whitesmoke;
  display: block;
  position: relative;
  padding-left: 50px;
}
header aside form input[type='radio'] {
  display: none;
}
header label span {
  width: 35px;
  height: 35px;
  position: absolute;
  overflow: hidden;
  line-height: 1;
  text-align: center;
  font-size: 1.1rem;
  font-weight: bold;
  left: 30px;
  top: 15%;
  cursor: pointer;
}
header aside form input[type='radio']:checked + span {
  color: lawngreen;
}
/* ...................................................................................................................... */
.main-body {
  width: 100%;
  height: 70%;
  flex-grow: 1;
  background-color: whitesmoke;
}
.main-body ul {
  width: 100%;
  height: 100%;
  margin: 140px 10px 60px 0;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.loading {
  font-size: 3rem;
  color: #303131;
  position: absolute;
  top: 300px;
  left: 50px;
}
.news-item {
  width: 400px;
  height: auto;
  margin: 10px;
  border: 0.5px solid #CDCFCF;
  animation: show 1s linear;
  text-decoration: none;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
@keyframes show {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0.1;
  }
  20% {
    opacity: 0.2;
  }
  30% {
    opacity: 0.3;
  }
  40% {
    opacity: 0.4;
  }
  50% {
    opacity: 0.5;
  }
  60% {
    opacity: 0.6;
  }
  70% {
    opacity: 0.7;
  }
  80% {
    opacity: 0.8;
  }
  90% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}
.news-item li {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  height: auto;
  list-style: none;
}
.news-item li p {
  width: 100%;
  height: 120px;
  padding: 5px 10px;
  color: #303131;
  font-weight: bold;
  font-size: 1.2rem;
}
.news-item li .author {
  height: 20px;
  color: firebrick;
}
.news-item li img {
  height: 100%;
  width: 100%;
  object-fit: contain;
  padding: 10px;
}
/* ...................................................................................................................... */
footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5%;
  background-color: rgb(6, 109, 150);
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
footer span,
p {
  color: whitesmoke;
  padding-right: 14px;
}